    import React, { Component } from "react";

    class Tags extends Component {
    state = {
        isShow: false,
    };
    onClickStatus = (event) => {
        const { isShow } = this.state;
        this.setState({ isShow: !isShow });
    };
    render() {
        const { isShow } = this.state;
        const { tags } = this.props;

        return (
        <>
            {tags && Object.keys(tags).length ? (
            <ul className="d-flex flex-wrap m-0">
                {tags.map((data, tagsIndex) => {
                return (
                    <li
                    className={`border me-2 mb-2 px-3 py-1 text-center ${
                        tagsIndex > 4 && !isShow ? "extra-tags d-none" : "d-inline"
                    }`}
                    >
                    {data}
                    </li>
                );
                })}
            </ul>
            ) : null}

            {!isShow ? (
            <a
                href="javascript:;"
                className={`tags-show-more align-item-center btn btn-outline-secondary w-100 btn-min-w btn-sm ${
                tags && tags.length > 0 && String(tags).split(",").length > 5
                    ? ""
                    : "d-none"
                }`}
                onClick={this.onClickStatus}
            >
                Show More
            </a>
            ) : (
            <a
                href="javascript:;"
                className="tags-show-less align-item-center btn btn-outline-secondary w-100 btn-min-w btn-sm"
                onClick={this.onClickStatus}
            >
                Show Less
            </a>
            )}
        </>
        );
    }
    }
    export default Tags;
