  import AuthorInfo from "../components/content/authorInfo";
  import ConnectButton from "./common/connectButton";
  import ShowMoreContent from "./common/showMoreContent";
  import Tags from "./Tags";

  const SliderItem = ({ assetItem, assetKey, active, newPicture }) => {
    
    console.log(assetItem, "assetItemassetItemassetItem");

    return active ? (
      <>
        <div class="influential_contributors_custom_modal p-5 sw_theme">
          <div className="influential-data-list">
            <div className="row align-items-start">
              <div className="col-md-6">
                <div className="d-flex justify-content-center">
                  <img src={assetItem.postFile} className="img-fluid rounded-8 w-100" width={"370px"} height={"370px"} />
                </div>
              </div>
              <div className="col-md-6">
                <div className="d-flex justify-content-between align-items-center mt-5">
                  <div className="d-flex align-items-center popup-author">
                    <AuthorInfo
                      picture={assetItem.authorImage}
                      username={assetItem.username}
                      date={""}
                      link={assetItem.link}
                    />
                  </div>
                  <div className="">
                    <i className={`fab ${assetItem.icon} font-size-h2`}></i>
                  </div>
                </div>

                <div className="mt-5">
                  <ShowMoreContent
                    classes={"influ_content"}
                    content={assetItem.content}
                    idIndex={0}
                    line={3}
                  />
                </div>
                {/* <div className="popup-tags-list mt-5">
                  <Tags tags={assetItem.tags} />
                </div> */}
              </div>
              {/* <div className="conn_status connnected popup-connnected mt-10 mb-5">
                <ConnectButton link={assetItem.link} />
              </div> */}
            </div>
          </div>
        </div>
      </>
    ) : null;
  };

  export default SliderItem;
